@import "../../global.scss";

.topbar {
  width: 100%;
  
  background-color: $primary;
  color: $secondary;
  position: fixed;
  top: 0;
  z-index: 999;
  transition: all .3s ease;

 
  .wrapper {
    padding: 20px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .left {
      display: flex;
      align-items: center;
      
      .itemContainer, .cinfo {
        display: flex;
        align-items: center;
        padding: 0 15px;
        
        a,
        span {
          text-decoration: none;
          color: $secondary;
          font-size: 15px;
          font-weight: 500;
       
         
          :hover {
            color: $accent;
            transition: all .3s ease;
          }
        }
        .icon {
          font-size: 1.1rem;
          margin-right: 5px;
        }
        .s-icon {
          font-size: 1.8rem;
          @include mobile {
           font-size: 1.3rem;
          
          }
        }
      }
      .cinfo{
        @include mobile {
          display: none;
        }

      }

      .logo {
        font-size: 1.8rem;
        font-weight: 700;
        text-decoration: none;
        color: $secondary;
        margin-right: 40px;
        span {
          color: $light;
        }
      }
    }

    .right{
        
        
        .hamburger{
            width:32px;
            height:25px;
            display:flex;
            flex-direction: column;
            justify-content: space-between;
            cursor: pointer;
            


            span{
                display:block;
                width:100%;
                height:3px;
                background-color: $secondary;
                transform-origin: left;
                transition: all .3s ease;
            }
        }
    }
  }
  &.active{
      background-color: $secondary;
      color:$primaryDark;
        .logo{
            color:$primaryDark !important;
            span{
                color:$accent !important;
            }
        }
        a,span{
            color:$primaryDark !important;
            :hover{
                color: $accent !important;
            }
        }
        .hamburger{
            span{
                &:first-child{
                    background-color: $primaryDark;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: $primaryDark;
                    transform: rotate(-45deg);
                }
            }
        }

  }
}
