@import "../../global.scss";



.menu{
    width: 400px;
    height:100vh;
    background-color: $primaryDark;
    position:fixed;
    top:0;
    right:-400px;
    z-index:88;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    transition: all .3s ease;

    &.active{
        background-color: $secondary;
        right:0;

    }
    ul{
        margin:0;
        padding:0;
        list-style: none;
        font-size: 30px;
        font-weight:400;
        color:$primary;
        width:60%;

        li{
            margin-bottom: 25px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }

            &:hover{
                font-weight: 500;
            }
        }
    }
}