@import "../../global.scss";

.portfolio{
    background-color: $secondary;
    display:flex;
    flex-direction:column;
    align-items:center;
    padding:30px;
   


    h2{
        font-size:50px;
        color:$primary;
        @include mobile {
           font-size: 30px;
        }
    }
    ul{
        margin:10px;
        padding:0;
        list-style:none;
        display:flex;
        @include mobile {
           flex-wrap: wrap;
           justify-content: center;
        }


        li{
            font-weight: 500;
            margin-right: 20px;
            padding: 7px 20px;
            border-radius:10px;
            cursor:pointer;
            color:$primaryDark;
            @include mobile {
                margin-right:0px
            }

            &.active{
                background-color: $accent;
                color:$light;
            }
        }
    }
    .container{
        width:70%;
        display:flex;
        align-items:center;
        justify-content: center;
        flex-wrap: wrap;
        @include mobile {
         width:100%;  
        }


        .item{
            
            text-decoration: none;
            width: 25%;
            height: 250px;
            border-radius:10px;
            
            margin: 15px 15px;
            display:flex;
            justify-content: center;
            align-items: center;
            color:$light;
            position:relative;
            transition:all .5s ease;
            @include mobile {
                
                width:100%;
                height: 150px;
            }
            

            h3{
                position:absolute;
                font-size:20px;
            }

            img{
               width: 100%;
               height:100%;
               object-fit: cover;
               z-index:1;
               object-position: top;
            }
            &:hover{
                background-color: $primaryDark;
                
                img{
                    opacity:0.1;
                    z-index:0;
                }
            }

        }
    }
  
}