@import "../../global.scss";

.contact {
  background-color: white;
  display: flex;
  h2{
    color: $primaryDark;
    // position:absolute;
   
    font-size:  50px;
    @include mobile {
     font-size: 30px;
     margin-bottom:1rem;
    }

  }

  @include mobile {
    flex-direction: column-reverse;
  } 

  .left {
    flex: 1;
    overflow: hidden;

    img {
      height: 100%;
    }
  }
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include mobile {
      display:column;
    }

    form {
      width: 90%;
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      @include mobile {
        display:column;
      }


      input {
        width: 70%;
        height: 30px;
        font-size: 16px;
        padding:20px;
        margin-bottom:2rem;

       
      }

      textarea {
        width: 70%;
        height: 150px;
        font-size: 16px;
        padding:10px;
        margin-bottom:1rem;
      }

      button {
        width: 150px;
        height: 30px;
        color: white;
        background-color: crimson;
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;

        &:focus {
          outline: none;
        }
      }

      span {
        color: green;
      }
    }
  }
}
