.app{
    height:100vh;

    .sections{
        width:100%;
        height:calc(100vh - 83.333px);
        background-color: #CCF381;
        position:relative;
        top: 83.333px;
        scroll-behavior: smooth;
     scroll-snap-type: y mandatory;
     scrollbar-width:none;
     &::-webkit-scrollbar{
         display:none;
     }
     
  
        >*{
            width:100vw;
            height:calc(100vh - 83.333px);
            scroll-snap-align: start ;
        }
    }
}