@import "../../global.scss";

.intro {
  background-color: $primary;
  display: flex;
  @include mobile {
    flex-direction: column;
    align-items: center;

  }

  .left {
    flex: 0.5;
    overflow: hidden;

    .imgContainer {
      width: 100%;
      // height: 100%;
      background-color: $secondary;
     border-top-right-radius: 50%;
     border-bottom-right-radius: 50%;
     border-top-left-radius: 50%;

            display: flex;
      justify-content: space-between;
      align-items: flex-end;
      // float: right;
      @include mobile {
        align-items: flex-start;
      }
      img {
        height: 80%;
        @include mobile {
          height:50%;
        }
        
      }
    }
  }

  .right {
    flex: 0.5;
    position: relative;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      color: $secondary;
      @include mobile {
       padding-left: 0;
       align-items: center;
      }


      h1 {
        font-size: 80px;
        margin: 0;
        color:$light;
        @include mobile {
       font-size: 40px;
        }
      }
      h2 {
        font-size: 35px;
      }
      h3 {
        font-size: 30px;
        @include mobile {
          font-size: 20px;;
        }

        span{
            font-size:inherit;
            color:$accent;
        }
        .ityped-cursor{
            animation:blink 1s infinite;
        }
        @keyframes blink{
            50%{
                opacity:1;
            }
            100%{
                opacity:0;
            }
        }
      }
     
    }
    a{
        position:absolute;
        bottom:30px;
        left:40%;
        animation:  arrow 0.7s ease-in infinite alternate;

        img{
            width:50px;
            
        }
        @keyframes arrow {
            0% {
              bottom: 60px;
            }
          
            100% {
              bottom: 30px;
            }
          }
        
    }
  }
}
